import React, { useState, useEffect } from 'react';
import ReactMapGL, { Marker, Popup } from 'react-map-gl';

import { listMosques } from './API';

const App = () => {

  const [mosques, setMosques] = useState([]);
  const [showPopup, setShowPopup] = useState({});
  // const [showMarker, setShowMarker] = useState({});
  const [viewport, setViewPort] = useState({
    width: '100vw',
    height: '100vh',
    latitude: 33.9174485,
    longitude: -118.0283703,
    zoom: 9
  });
  const TOKEN = "pk.eyJ1IjoibW9zcXVlZnVuZCIsImEiOiJja2N3ank1ZXUwMnVnMnNwbXh2eXB5OG04In0.J56Th828d2K-c5sJ8tLsjg";

  const mosqueImages = [
    require('./images/mosque1.png'),
    require('./images/mosque2.png'),
    require('./images/mosque3.png'),
    require('./images/mosque4.png'),
    require('./images/mosque5.png'),
    require('./images/mosque6.png'),
  ];

  useEffect(() => {
    (async () => {
      document.title = "Mosque Fund Map";
      const mosques = await listMosques();
      for (const mosque of mosques) {
        mosque.iconUrl = mosqueImages[Math.floor(Math.random() * 6)];
        // setShowMarker({ ...showMarker, [mosque.placeId]: true });
      }
      setMosques(mosques);
    })();
  }, []);

  const showAddMarkerPopup = (event) => {
    console.log(event);
  }

  return (
    <ReactMapGL
      mapboxApiAccessToken={TOKEN}
      {...viewport}
      mapStyle="mapbox://styles/mosquefund/ckczhvmog02781io8pfq9abpt"
      onViewportChange={setViewPort}
      onDblClick={showAddMarkerPopup}
    >
      {
        mosques.map(entry => (
          <>
            <Marker
              key={entry.placeId}
              latitude={Number(entry.latitude)}
              longitude={Number(entry.longitude)}
              style={{
                zIndex: '-1'
              }}
            >
              <div onClick={() => setShowPopup({
                // ...showPopup,
                [entry.placeId]: true
              })}>
                <img className="marker"
                  alt="marker"
                  style={{
                    width: `${7.5 * viewport.zoom}px`,
                    height: `${7.5 * viewport.zoom}px`,
                  }}
                  src={entry.iconUrl} />
              </div>
            </Marker>
            {
              showPopup[entry.placeId] ? (
                <Popup
                  latitude={Number(entry.latitude)}
                  longitude={Number(entry.longitude)}
                  style={{
                    zIndex: '1'
                  }}
                  closeButton={true}
                  closeOnClick={false}
                  dynamicPosition={true}
                  onClose={() => setShowPopup({ ...showPopup, [entry.placeId]: false })}
                  anchor="top"
                >
                  <div className="popup">
                    <h3>{entry.name}</h3>
                    <p>{entry.fullAddress}</p>
                    {entry.website ? <p>Website: <a href={`https://www.${entry.website}`} target="_blank" rel="noopener noreferrer">{entry.website}</a></p> : null}
                    {Number(entry.rating) > 4 ? <p>Rating: {entry.rating}</p> : null}

                  </div>
                </Popup>
              ) : null
            }
          </>
        ))
      }
    </ReactMapGL >
  );
}

export default App;
